
* {
  margin: 0px;
  padding: 0px;
  font-family: Arial, Helvetica, sans-serif;
}
body {
  background: #fff;
  color: #333;
}
a {
  cursor: pointer;
}
input::-webkit-input-placeholder {
  color: #ccc;
}
input,
textarea,
select,
button {
  outline: none;
  resize: none;
}
textarea {
  display: block;
}
.header {
  background: #f5f5f5;
  position: fixed;
  z-index: 10;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 40px;
  border: 1px solid #eee;
}
.header-main {
  width: 1100px;
  margin: 0 auto;
}
.header-main-menu {
  position: absolute;
  bottom: 0px;
}
.main {
  width: 100%;
}
.main-layout {
  width: 1100px;
  margin: 0 auto;
  padding-top: 60px;
  padding-bottom: 20px;
}
.main-layout::after {
  content: "";
  display: block;
  clear: both;
  height: 0px;
  overflow: hidden;
}
.main input,
.main .input {
  color: #333;
  font-size: 14px;
  line-height: 18px;
  background: #fff;
  border: 1px solid #ddd;
  padding: 5px;
}
.main textarea,
.main .textarea {
  color: #333;
  font-size: 14px;
  line-height: 18px;
  width: 770px;
  height: 144px;
  background: #fff;
  border: 1px solid #ddd;
  word-break: break-all;
  overflow: auto;
  margin: 2px 0px;
  padding: 5px;
}
.main input:focus,
.main .input:focus,
.main textarea:focus,
.main .textarea:focus {
  border: 1px solid #999;
}
.main button {
  font-size: 14px;
  color: #666;
  border-radius: 4px;
  background: #fff;
  border: 1px solid #ddd;
  padding: 2px 10px;
  display: inline-block;
  line-height: 24px;
  cursor: pointer;
}
.main button:hover {
  background: #eee;
}
.main button.submit {
  background: #888;
  color: #fff;
}
.main button.submit:hover {
  background: #666;
}
.main-search input {
  background: #fff;
  border: 1px solid #ddd;
  font-size: 14px;
  color: #666;
  width: 200px;
  height: 24px;
  line-height: 24px;
  padding: 3px 10px 3px 30px;
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-position: 5px 7px;
}
.main-search input:focus {
  border-color: #999;
}

.l-menu-nav[data-v-2f6876d6] {
  margin: 0px;
  padding: 0px;
  list-style: none;
}
.l-menu-nav[data-v-2f6876d6]::after {
  content: "";
  display: block;
  clear: both;
  height: 0px;
  overflow: hidden;
}
.l-menu-nav li[data-v-2f6876d6] {
  float: left;
  height: 32px;
  line-height: 32px;
  font-size: 16px;
  margin: 0px 10px;
  cursor: pointer;
  color: #999;
  padding: 0px 2px;
}
.l-menu-nav li[data-v-2f6876d6]:hover {
  color: #333;
}
.l-menu-nav li.select[data-v-2f6876d6] {
  color: #333;
  border-bottom: 3px solid #333;
}
.l-menu-line[data-v-2f6876d6] {
  height: 0px;
  overflow: hidden;
  border-bottom: 3px solid #333;
  position: absolute;
  margin-top: -3px;
}
































































.main-tool .item {
  background: #eee;
  color: #666;
  font-size: 14px;
  border-radius: 2px;
  display: inline-block;
  height: 24px;
  line-height: 24px;
  padding: 0px 6px;
  margin-right: 8px;
  margin-bottom: 8px;
}
.main-tool .item:hover {
  background: #666;
  color: #eee;
}
.main-tool-list {
  padding-top: 15px;
}
.main-tool-item h3 {
  margin-bottom: 15px;
}
.main-form {
  line-height: 26px;
}
.main-form input {
  width: 600px;
  padding: 3px 2px;
  color: #333;
  font-size: 14px;
  line-height: 18px;
  background: #fff;
  border: 1px solid #ddd;
  padding: 5px;
}
.main-form input:focus,
.main-form .input:focus,
.main-form textarea:focus,
.main-form .textarea:focus {
  border: 1px solid #999;
}
.main-form textarea,
.main-form .textarea {
  color: #333;
  font-size: 14px;
  line-height: 18px;
  width: 600px;
  height: 72px;
  background: #fff;
  border: 1px solid #ddd;
  word-break: break-all;
  overflow: auto;
  margin: 2px 0px;
  padding: 5px;
}

.go-back[data-v-5d615b90] {
  margin-bottom: 15px;
}
.go-back a[data-v-5d615b90] {
  font-size: 14px;
  color: #666;
}
.go-back a[data-v-5d615b90]:hover {
  color: #333;
}























































